
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 20px 0px;
  max-width: 2000px;
  max-height: 1500px;
  width: 100%;
  height: 80vh;
  
}

.wrap {
  box-shadow: rgba(21, 20, 20, 0.16) 6px 12px 23px;
  max-width: 1600px;
  max-height: 1400px;
  justify-content: center;
  align-items: center;
  width: 45%;
  padding: 30px 25px;
  margin: 19px;
  border-radius: 13px;
}

.h1 {
  text-align: center;
  font-weight: bold;
  font-size: 34px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

input[type='text'] {
  display: inline-block;
  width: 97%;
  box-sizing: border-box;
  padding: 9px 15px;
  border-radius: 8px;
  border: none;
  margin: 5px 10px;
  background-color: rgb(246, 249, 251);
  border-left: 2px solid rgb(146, 203, 146) ;in: 9px 9px;
  font-size: 18px;

}

input[type='submit']{
  background-color: rgb(75, 75, 189);
  color: white;
  font-size: 15px;
  margin: 4px 10px;
  padding: 6px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  
}
button{
  background-color: rgb(211, 76, 87);
  color: white;
  font-size: 15px;
  margin: 4px 10px;
  padding: 6px 7px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
input:hover[type='submit'] {
  background-color: rgb(83, 171, 130);
}

label {
  font-size: 24px;
  font-weight: bold;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin: 9px 9px;
}

.list{
  box-shadow: rgba(210, 108, 6, 0.16) 3px 6px 17px;
  max-width: 1600px;
  max-height: 1400px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 18px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 100%;
  padding: 1px 9px;
  margin: 3px 10px;
  border-radius: 10px;
  box-sizing: border-box;
}
